"use client";
import React from "react";
import { Avatar, Badge, BadgeProps, Button, Drawer, IconButton, Stack } from "@mui/material";
import {
  DarkModeOutlined,
  LightModeOutlined,
  NotificationsActiveOutlined,
  NotificationsNoneOutlined,
  ShoppingCartOutlined,
} from "@mui/icons-material";
import { styled } from '@mui/material/styles';
import CartToggleDrawer from "@/public/utils/helpers/cart/cart-toggleDrawer";

import No from "@/public/utils/helpers/notification/notification-toggleDrawer";
const TopBar = (prop: any) => {
  const [cart, setCart] = React.useState(false);
  const [lightMode, setLightMode] = React.useState("light");
  const [notification, setNotification] = React.useState(false);
  React.useEffect(() => {
    const savedMode = localStorage.getItem("theme");
    if (savedMode) {
      setLightMode(savedMode as "light" | "dark");
    }
    setNotification(true);
    // fetch("/api/check-url")
    //         .then((res) => res.json())
    //         .then((data) => {
    //             if (data.blocked) {
    //                 setBlocked(true);
    //             }
    //         });
  }, []);
  React.useEffect(() => {
    if (lightMode) {
      localStorage.setItem("theme", lightMode);
    }
  }, [lightMode]);
  const toggleDrawer = (toggleData: any ,open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
    if (
      event.type === "keydown" &&
      ((event as React.KeyboardEvent).key === "Tab" ||
        (event as React.KeyboardEvent).key === "Shift")
    ) {
      return;
    }

    toggleData(open);
  };
  const setLightDark = () => {
    setLightMode(lightMode === "light" ? "dark" : "light");
  };
  
  const setNoti = () => {
    setNotification(!notification);
    toggleDrawer();
  };
  return (
    <div className="top-bar max-w-full text-right">
      <IconButton aria-label="cart" onClick={toggleDrawer(setCart,true)}>
        <Stack spacing={4} direction="row" sx={{ color: 'action.active' }}>
          <Badge color="secondary" badgeContent={88} max={100}>
            <ShoppingCartOutlined />
          </Badge>
        </Stack>
      </IconButton>
      <Drawer anchor={"right"} open={cart} onClose={toggleDrawer(setCart,false)}>
        {CartToggleDrawer(toggleDrawer)}
      </Drawer>
      <Button onClick={setLightDark}>
        {lightMode === 'light' ? <LightModeOutlined /> : <DarkModeOutlined />}
      </Button>
      <Button onClick={setNoti}>
        {notification ? <NotificationsNoneOutlined /> : <NotificationsActiveOutlined />}
      </Button>

      <IconButton aria-label="avarta" onClick={toggleDrawer(setCart,true)}>
        <Stack spacing={4} direction="row" sx={{ color: 'action.active' }}>
          <Avatar alt="Remy Sharp" src="/images/demo/1.png" />
        </Stack>
      </IconButton>
      <Drawer anchor={"right"} open={cart} onClose={toggleDrawer(setCart,false)}>
        {NotificationToggleDrawer(toggleDrawer)}
      </Drawer>
    </div>
  );
};
export default TopBar;
